	import lotusAddress from "@/components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue"
	export default {
		data() {
			return {
				addressData: {
					consignee: '',
					phone: '',
					province: '',
					city: '',
					county: '',
					address: '',
					isDefault: 0,
					def: false
				},
				lotusAddressData:{
					visible:false,
					provinceName:'',
					cityName:'',
					townName:'',
				},
			}
		},
		onLoad(option){
			let title = '新增收货地址';
			if(option.type==='edit'){
				title = '编辑收货地址'
				this.addressData = JSON.parse(option.data)
				this.addressData.isDefault = parseInt(this.addressData.isDefault);
			}
			this.manageType = option.type;
			uni.setNavigationBarTitle({
				title
			})
		},
		methods: {
			switchChange(e){
				this.addressData.isDefault = e.detail.value?1:0
				this.addressData.def = e.detail.value
			},
			
			choseValue(res){
            //res数据源包括已选省市区与省市区code
				this.lotusAddressData.visible = false;//visible为显示与关闭组件标识true显示false隐藏
				if(res.isChose){
					console.log(res)
					this.lotusAddressData.provinceName = res.provice;//省
					this.lotusAddressData.cityName = res.city;//市
					this.lotusAddressData.townName = res.town;//区
					
					//赋值到addressData
					this.addressData.province = res.provice
					this.addressData.city = res.city
					this.addressData.county = res.town

				}
			},
			
			//提交
			confirm(){
				const that = this
				let data = this.addressData;
				if(!data.consignee){
					this.$api.msg('请填写收货人姓名');
					return;
				}
				if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)){
					that.$api.msg('请输入正确的手机号码');
					return
				}
				if (!data.province) {
					that.$api.msg('请选择省市区');
					return
				}
				if (!data.city) {
					that.$api.msg('请选择二级城市')
					return
				}
				if (!data.county) {
					that.$api.msg('请选择三级区或县')
					return
				}
				if(!data.address){
					that.$api.msg('请输入详细地址');
					return
				}
				
				//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
				
				//this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
				if (that.manageType === 'edit') {
					that.$api.request('address', 'updateAddress', {
						...that.addressData,
						addressId : that.addressData.id
					}).then(res => {
						that.$api.prePage().refreshList(data, that.manageType);
						uni.navigateBack()
					})
				} else {
					that.$api.request('address', 'addAddress', that.addressData).then(res => {
						that.$api.prePage().refreshList(data, that.manageType);
						uni.navigateBack()
					})
				}
				
			},
		},
		components:{
			"lotus-address":lotusAddress
		},
	}
